body{
  margin: 0;
}
.contact-info{
  display: flex;
 justify-content: space-evenly;
 width: 100%;
 /* padding:2% 0; */
 align-items: center;
}
.logo{
  width:70px;
  height: 70px;
  object-fit: cover;
}
.contact{
  display: flex;
  align-items: center;
  justify-content:baseline;
  width: 100%;
}
.contact i{
  color: orangered;
  padding: 20px;
}
.contact div{
  line-height: 0;
}
button{
  width: 150px;
  height: 50px;
  background-color: orangered;
  border: none;
  color:white;
  font-size: large;
  font-weight: 700;
}
button:hover{
  background-color: white;
  color:#fd5f00;
  border: 2px solid #fd5f00;
}
nav{
  margin: auto;
  width: 75%;
  display: flex;
  justify-content:space-between;
  align-items: center;
  background-color: #0d1128;
  padding: 1%;
  margin-bottom: 0;
}
nav ul{
  display: flex;
  justify-content: space-between;
  align-items: center;
}
nav ul li{
  list-style: none;  
}
nav ul li a{
  color: white;
  padding: 0 20px;
  text-decoration: none;   
}
a:hover{
  color: orangered;
}
nav input{
  padding: 1%;
  border-radius: 5px;
}
.headline{
text-align: center;
width: 100%;
height:79vh;
margin: 0;
background-image: url("students.jpg");
background-origin: content-box;
background-position: center;
object-fit: cover;
position: relative;
background-size: cover;
z-index: -2;
opacity: 0.8;
}
.headline .orange-tint{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color:rgba(211, 85, 12, 0.5); /* Adjust color and opacity as needed */
  mix-blend-mode: color;
  z-index: -1;
}
.headline .greeting{
  display: flex;
  flex-direction: column;
  align-items:center;
  justify-content: space-evenly;
  width: 70%;
  height: 80%;
  margin: auto;
  font-family: "Anta", sans-serif;
  font-weight: 400;
  font-style: normal;
}
.headline h1{
 margin: 0;
  text-shadow: 2px 4px 4px rgba(46,91,173,0.6);
color:white;
font-size: 50px;
}
.headline p{
margin: 0;
 font-weight: 600;
color:white;
font-size: 30px;
font-family: "Great Vibes", cursive;
font-weight: 400;
font-style: normal;
}
.about-college{
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: center;
}
.box{
  text-align: center;
  flex: 25%;
  height: 300px;
  background-color: #0d1128;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 20px;
  color: white;
}
.box i{
  margin: 5px;
  font-size: 50px;
}
.box h4{
  margin: 0;
}
.box p{
  width: 80%;
}
#orange{
  background-color: #fd5f00 ;
}
footer{
  width:100%;
  background-color: white;
  text-align: center;
  font-weight: 400;
  font-style: normal;
}
.register{
  width:60%;
  margin: auto;
  text-align: center;
}
.register h1{
  font-family: "Anta", sans-serif;  
}
.register p{
  font-family: "Anta", sans-serif;  
  font-size: 20px;
}
.contact-us{
  display: flex;
  justify-content: space-around;
  align-items: center;
   background: linear-gradient(to top, #0d1128, white);
}
.contact-us img{
  width :500px;
}
form {
   background-color: #0d1128;
  max-width: 500px;
  margin: 20px auto;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}
label {
  font-weight: bold;
  color: white;
}
input[type="text"],
input[type="tel"],
input[type="email"],
textarea {
  width: 100%;
  padding: 10px;
  margin: 5px 0;
  border: 1px solid #ccc;
  background-color: transparent;
  color: white;
  box-sizing: border-box;
  border: none;
  border-bottom: 1px solid #fd5f00;
  resize: none;
}
textarea{
  resize: none;
}
input[type="radio"] {
  margin-right: 5px;
}
input[type='radio']:checked:after {
  width: 12px;
  height: 12px;
  border-radius: 15px;
  top: -2px;
  left: -1px;
  position: relative;
  background-color: #fd5f00;
  content: '';
  display: inline-block;
  visibility: visible;
  border: 2px solid white;
}
input[type="submit"] {
  margin-left: 34%;
  margin-top: 2%;
  background-color: #fd5f00;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
}
input[type="submit"]:hover {
  background-color: #fd5f00;
}
textarea {
  resize: vertical;
}
div.radio-group {
  margin-bottom: 10px;
}
p {
  margin: 10px 0;
  font-weight: bold;
}